.custom-slide {
  width: 200px;
  height: 200px;
  border-radius: 20px;
  border: 1px solid #e6e6e6;
  display: flex;
  justify-content: center;
  align-items: center;
  background-size: cover;
}

.custom .custom-slide-content {
  background-color: rgba(0, 0, 0, 0.385);
  color: white;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; 
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  
}

.mySwiper {
  width: 100%;
  height: 250px; /* Or whatever height fits your slide */
  overflow: visible;
}
